
.container-terminal {
    &-container{
        padding: 40px 60px 60px 45px;
    }
    &__content{
        display: flex;
        gap: 10px;
        flex-wrap: wrap;
        flex-direction: row;
    }
    &__title{
        font-weight: 700;
        font-size: 28px;
        line-height: 38px;
        color: #33BAEC;
        text-align: center;
        margin-bottom: 50px;
    }
    &__item {
        background: #F9F8F8;
        border-radius: 20px;
        max-width: 313px;
        padding: 20px 10px;
        &__up{
            display: flex;
            align-items: center;
            margin-bottom: 20px;
            &-img{
                margin-right: 6px;
            }
            &-title{
                font-weight: 600;
                font-size: 18px;
                line-height: 25px;
            }
        }
        &__down{
            margin-left: 15px;
            &-tel{
                display: flex;
                gap: 10px;
                
                &__title{
                    font-weight: 600;
                    font-size: 17px;
                    line-height: 23px;
                    text-indent: 1px;
                    color: #33BAEC;
                }
                &__text{
                    font-weight: 300;
                    font-size: 17px;
                    line-height: 23px;
                }
                &__phone{
                    font-weight: 300;
                    font-size: 17px;
                    line-height: 23px; 
                }
                &:not(:last-child){
                    margin-bottom: 5px;
                }
            }
        }
    }
}
