.terminal-container-services {
    font-weight: 400;
    font-size: 17px;
    line-height: 23px;
    text-indent: 1px;
    font-feature-settings: 'liga' off;
    color: #000000;

    &-container{
        padding: 0px 60px 50px;
        display: flex;
    }
    &__content{
        width: 100%;
        display: flex;
        flex-direction: column;
        &-title{
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            color: #33BAEC;
            margin-bottom: 35px;
        }
        &-list{
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
        &-item{
            
        }
    }
    &__side{
        max-width: 360px;
        width: 100%;
        &__item{
            background: #F9F8F8;
            border-radius: 12px;
            padding: 35px 30px;
            &:not(:last-child){
                margin-bottom: 15px;
            }
            &-title{
                font-weight: 700;
                font-size: 22px;
                line-height: 30px;
                color: #33BAEC;
                margin-bottom: 10px;
            }

            &-descr{
                font-weight: 400;
                font-size: 14px;
                line-height: 19px;
                text-transform: uppercase;
                color: #000000;
            }
        }

    }

}