.news-section {
    &-main{
        width: 100%;
    }
    &-container{
        padding: 45px 95px;
    }
    &__title{
        font-weight: 700;
        font-size: 36px;
        line-height: 49px;
        color: #33BAEC;
        margin-bottom: 50px;
    }
    &__content{
        display: flex;
        flex-wrap: wrap;
        row-gap: 50px;
        column-gap: 35px;
        margin-bottom: 115px;
    }
    &__item{
        padding: 25px 23px;
        max-width: 415px;
        height: 260px;
        overflow-y: scroll;
        background: #F9F8F8;
        border-radius: 12px;
        

        &-data{
            font-weight: 300;
            font-size: 17px;
            line-height: 23px;
            opacity: 0.5;
            margin-bottom: 10px;
            width: 80%;
        }
        &-title{
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            color: #33BAEC;
            margin-bottom: 14px;
            width: 80%;
        }
        &-descr{
            font-weight: 300;
            font-size: 17px;
            line-height: 23px;
            color: #000000;
            width: 80%;
        }
    }
    &__pagination {
        border-top: 0.4px solid #C9C9C9;

        &__pages{
            text-align: center;
        }
        &-btn {
            padding: 14px 45px;

            font-weight: 600;
            font-size: 18px;
            line-height: 25px;
            color: #33BAEC;
            &.active {
                border-top: 3px solid #33BAEC;
            }
        }
    }
}