.terminal-container-treatment {

    &-container{
        padding: 0px 60px 70px;
    }
    &-title{
        font-weight: 700;
        font-size: 22px;
        line-height: 30px;
        color: #33BAEC;
        margin-bottom: 35px;
    }
    &__content{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        row-gap: 20px;
        column-gap: 30px;
        
    }
    &__item{
        display: flex;
        align-items: center;
        padding: 10px 20px;
        gap: 40px;
        width: calc(50% - 15px);

        background: #F9F8F8;
        border-radius: 12px;

        &-img{

        }
        &-descr{
            font-weight: 400;
            font-size: 22px;
            line-height: 30px;
            color: #000000;
        }
    }
}