.port-left{

    &-container{
        display: flex;
        flex-direction: column;
        height: 100%;
    }
    &__title{
        font-weight: 700;
        font-size: 22px;
        line-height: 30px;
        color: #33BAEC;
        margin-bottom: 35px;
    }
    &__content{
        flex: 1;
        display: flex;
        flex-direction: column;
        // flex-wrap: wrap;
        justify-content: space-between;
    }
    &__item{
        display: flex;
        align-items: center;
        padding: 10px 20px;
        gap: 70px;
        background: #F9F8F8;
        border-radius: 12px;

        &-img{

        }
        &-descr{
            display: flex;
            flex-direction: column;
            justify-content: center;
            &-title{
                font-weight: 700;
                font-size: 22px;
                line-height: 30px;
                color: #314781;
            }
            &-text{
                font-weight: 400;
                font-size: 14px;
                line-height: 19px;
                text-transform: uppercase;
                color: #20305B;
            }
        }
    }
}