.form {
    font-style: normal;
    font-weight: 300;
    font-size: 17px;
    line-height: 23px;
    text-indent: 1px;
    padding-bottom: 100px;
    &-container{
        padding: 40px 60px 60px 45px;
    }
    &__title{
        font-weight: 700;
        font-size: 28px;
        line-height: 38px;
        color: #33BAEC;
        text-align: center;
        margin-bottom: 50px;
    }
    &__content{
        background: #F9F8F8;
        border-radius: 20px;
    }
    &__form{
        padding: 27px;
        
        
        &__content{
            display: flex;
            flex-wrap: wrap;
            column-gap: 10px;
            margin-bottom: 18px;
        }
    }
    &-text{
        
    }
    &-tel{
       
    }
    &-email{
        
    }
    &__input{
        background: #FFFFFF;
        border-radius: 10px;
        border: none;
        padding: 18px;
        flex: 1
    }
    &-textarea {
        background: #FFFFFF;
        border-radius: 10px;
        border: none;
        padding: 18px;
        width: 100%;
        margin-bottom: 18px;
        resize: none;
    }
    &__btn {
        position: relative;
        background: #33BAEC;
        border: 1px solid #33BAEC;
        border-radius: 35px;
        display: flex;
        align-items: center;
        padding: 10px 20px;
        margin: 0 auto;
        &-img{
            margin-right: 20px;
        }
        &-text{
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            color: #FFFFFF;
        }
    }
}