.terminal-cargo-main-left {
    font-weight: 400;
    font-size: 17px;
    line-height: 23px;
    text-indent: 1px;
    font-feature-settings: 'liga' off;
    color: #000000;

    &-container{
        // padding: 0px 60px 50px;
        // display: flex;
        height: 100%;
    }
    &__content{
        width: 100%;
        display: flex;
        flex-direction: column;
        margin-bottom: 50px;
        &-title{
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            color: #33BAEC;
            margin-bottom: 35px;
        }
        &-list{
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            
        }
        &-item{
            
        }
    }
    &__treatment{

        &-title{
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            color: #33BAEC;
            margin-bottom: 35px;
        }
        &__content{
            display: flex;
            flex-direction: column;
            // flex-wrap: wrap;
            // justify-content: space-between;
            row-gap: 10px;
            column-gap: 30px;
            
        }
        &__item{
            display: flex;
            align-items: center;
            padding: 10px 20px;
            gap: 40px;
            width: 85%;
            background: #F9F8F8;
            border-radius: 12px;
    
            &-img{
    
            }
            &-descr{
                font-weight: 400;
                font-size: 22px;
                line-height: 30px;
                color: #000000;
            }
        }
    }

}