.port-right{
    flex: 1;
    &-container{
        display: flex;
        flex-direction: column;
        
    }
    &__title{
        font-weight: 700;
        font-size: 22px;
        line-height: 30px;
        color: #33BAEC;
        margin-bottom: 35px;
    }
    &__content{
        display: flex;
        flex-direction: column;
        gap: 15px;
    }
    &__item{
        background: #F9F8F8;
        border-radius: 12px;
        padding: 35px 30px;

        
        &-container{
            display: flex;
            flex-direction: column;
            gap: 5px;
        }
        &-title{
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            color: #33BAEC;
        }
        &-descr{
            font-weight: 400;
            font-size: 14px;
            line-height: 19px;
            text-transform: uppercase;
            color: #000000;
        }
        &-item{
            color: #33BAEC;
        }
    }
}