.terminal-cargo-main-right {
    max-width: 360px;
    width: 100%;

    &-container{
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    &__item{
        background: #F9F8F8;
        border-radius: 12px;
        padding: 35px 30px;
        
        
        &:last-child{
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            flex: 1;
        }
        &:not(:last-child){
            margin-bottom: 15px;
        }
        &-title{
            font-weight: 700;
            font-size: 22px;
            line-height: 30px;
            color: #33BAEC;
            margin-bottom: 10px;
        }

        &-descr{
            font-weight: 400;
            font-size: 14px;
            line-height: 19px;
            text-transform: uppercase;
            color: #000000;
        }
    }
}