.terminal-container-picture{


    &-container {
        padding: 55px 60px;
    }
    &-img{
        width: 952px;
        height: 509px;
        margin-bottom: 40px;
    }
    &-descr{
        font-weight: 400;
        font-size: 18px;
        line-height: 25px;
        color: #000000;
        &:not(:last-child){
            margin-bottom: 20px;
        }
    }
}